.header {
  display: flex;
  padding: 0 0.6rem;

  &__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & h4 {
      position: relative;
      font-style: italic;
      font-size: x-large;
      font-family: 'Amiri';
      font-weight: 700;
      margin: 0;
    }
  }

  &__nav {
    margin-left: auto;
    width: 30%;
    @include flex-center;
    justify-content: space-between;

    &--item {
      padding: 0 1rem;
      font-family: 'Open Sans';
      & p {
        cursor: pointer;
        @include link-hover-effect;
      }
    }
  }
}

@media (max-width: 576px) {
  .header {
    &__title {
      display: none;
    }
    &__nav {
      width: 100%;
    }
  }
}
