.hero {
  height: 90vh;
  display: flex;
  @include container;

  &__col-1 {
    width: 40%;
    &--title {
      font-family: 'Roboto';
      font-size: 4rem;
    }

    &--subtitle {
      color: $light-color;
      font-family: 'Montserrat';
    }

    &--icons {
      font-size: xx-large;

      &-icon {
        margin-right: 0.6rem;

        &:nth-child(1) {
          color: #0077b5;
        }
        &:nth-child(2) {
          color: #333;
        }
        &:nth-child(3) {
          color: #1da1f2;
        }
      }
    }

    &--buttons {
      margin-top: 1rem;

      &-btn {
        @include btn-rules;
        @include btn-wave-effect;
        font-family: 'Montserrat';

        & span {
          margin: 0 0.4rem 0 0;
        }
      }
    }
  }

  &__col-2 {
    width: 60%;
    @include flex-center;
  }
}

.about {
  display: flex;
  @include container;
  margin-top: 2rem;

  &__col-1 {
    width: 40%;
    @include flex-center;
    & img {
      box-shadow: 25px 20px 0px -3px $primary-color;
      width: 320px;
    }
  }

  &__col-2 {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 2rem;
    & h1 {
      font-family: 'Roboto';
      font-weight: 500;
      @include underline;
    }

    & p {
      font-family: 'Open Sans';
      color: $light-color;
      margin-top: 0;
      width: 70%;
      & a {
        text-decoration: underline;
      }
    }
  }
}

.tech {
  display: flex;
  @include container;
  margin-top: 4rem;
  &__col-1 {
    width: 60%;
    & h1 {
      font-family: 'Roboto';
      font-weight: 500;
      @include underline;
    }

    & p {
      font-family: 'Open Sans';
      color: $light-color;
    }

    & ul {
      font-family: 'Open Sans';
      color: $light-color;
      margin-top: 0;
      width: 70%;
      list-style-type: circle;
    }

    &--icons {
      font-size: 4rem;
      cursor: pointer;
      @include flex-center;
      justify-content: flex-start;

      &-icon {
        &:hover {
          transform: scale(1.1);
          transition: 0.2s;
        }

        &:nth-child(1):hover {
          color: #edd718;
        }
        &:nth-child(2):hover {
          color: #5ed3f3;
        }
        &:nth-child(3):hover {
          color: #87bf00;
        }
        &:nth-child(4):hover {
          color: #82be5e;
        }
        &:nth-child(5):hover {
          color: #f7c52a;
        }
        &:nth-child(6):hover {
          color: #2962ff;
        }
        &:nth-child(7):hover {
          color: #102a47;
        }
        &:nth-child(8):hover {
          color: #e94e31;
        }
        &:nth-child(9):hover {
          color: black;
        }
      }
    }
  }

  &__col-2 {
    width: 40%;
  }
}

.achievements {
  display: flex;
  @include container;
  margin-top: 4rem;

  & h1 {
    font-family: 'Roboto';
    font-weight: 500;
    @include underline;
  }
  &__col-1 {
    width: 100%;
    &--container {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      grid-gap: 50px;
    }
  }

  &__card {
    @include flex-center;
    flex-direction: column;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
    padding: 1rem 0.4rem;
    border-radius: 4px;
    &--col1 {
      @include flex-center;
      padding: 0.2rem;
      & img {
        height: 150px;
      }
    }
    &--col2 {
      & h4 {
        font-family: 'Open Sans';
        font-weight: 300;
        font-size: x-large;
        margin: 0 0 8px 0;
        text-align: center;
      }
      & p {
        font-family: 'Open Sans';
        text-align: center;
        margin-top: 3px;
        color: $light-color;
      }
      &-links {
        @include flex-center;
        flex-flow: row wrap;
        & a {
          background-color: $primary-color;
          color: white;
          margin: 0 10px 10px 10px;
          font-size: small;
          border-radius: 14px;
          letter-spacing: 1px;
          text-transform: uppercase;
          padding: 4px 8px;
          font-family: 'Montserrat';
          &:hover {
            background-color: white;
            color: $primary-color;
            transition: 0.3s;
          }
        }
      }
    }
  }
}

.projects {
  display: flex;
  @include container;
  margin-top: 4rem;

  &__col-1 {
    width: 60%;

    & h1 {
      font-family: 'Roboto';
      font-weight: 500;
      @include underline;
    }

    &--project-container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      gap: 10px;

      &-project {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        padding: 0.2rem 1rem;
        border-radius: 4px;
        margin: 1rem;

        &:hover {
          transition: 0.6s;
          box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
          transition-timing-function: ease;
        }

        & h3 {
          font-family: 'Open Sans';
          font-weight: 300;
          font-size: x-large;
        }
        & p {
          font-family: 'Open Sans';
          color: $light-color;
        }
        & div {
          font-size: 1.4rem;
          padding: 4px;
          display: flex;

          & button {
            @include btn-rules;
            padding: 8px 16px;
            margin: 0 4px 0 0;
            @include btn-wave-effect;
          }
        }
      }
    }
  }

  &__col-2 {
    width: 40%;
    @include flex-center;
    flex-direction: column;

    & p {
      font-family: 'Open Sans';
      width: 80%;
      color: $light-color;
      position: relative;
      left: 10%;
      text-align: left;
    }
    button {
      @include btn-rules;
      background-color: #333;
      position: relative;
      left: 10%;
      font-family: 'Montserrat';
      @include btn-wave-effect;
      @include flex-center;
    }
  }
}

.connect {
  @include container;
  @include flex-center;
  flex-direction: column;
  height: 70vh;

  & h1 {
    font-family: 'Roboto';
    font-weight: 300;
    margin: 0;
  }

  & p {
    font-family: 'Open Sans';
    color: $light-color;
    margin: 0;
    & span {
      font-size: x-small;
    }
  }

  &__info {
    @include flex-center;
    flex-direction: column;
    margin-top: 0.6rem;
    & p {
      @include flex-center;
    }
    &--icon {
      margin-right: 4px;
    }
  }
  &__social {
    margin-top: 1rem;
    font-size: 1.8rem;
    @include flex-center;

    & a {
      margin: 0 0.2rem;
    }
  }
}

.scroll-to-top {
  @include btn-rules;
  position: fixed;
  bottom: 10px;
  right: 10px;
  font-size: xx-large;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  padding: 0;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

@media (max-width: 800px) {
  .hero {
    flex-direction: column;
    width: 90%;
    &__col-1 {
      width: 100%;
      height: 100%;
      &--title {
        font-family: 'Roboto';
        font-size: 3.8rem;
      }
    }

    &__col-2 {
      width: 100%;
      margin-top: 2rem;
    }
  }

  .about {
    flex-direction: column;
    width: 90%;
    margin-top: 2rem;

    &__col-2 {
      width: 100%;
      padding-left: 0;
      & p {
        width: 100%;
      }
    }
    &__col-1 {
      order: 2;
      width: 70%;
      margin: auto;
      & img {
        width: 100%;
      }
    }
  }

  .tech {
    flex-direction: column;
    width: 90%;
    //margin-top: 1.6rem;

    &__col-2 {
      width: 100%;
      padding-left: 0;
      & p {
        width: 100%;
      }
    }
    &__col-1 {
      width: 100%;
      margin: auto;
      & img {
        width: 100%;
      }
    }
  }

  .projects {
    flex-direction: column;
    width: 90%;
    &__col-1 {
      width: 100%;
      &--project-container {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
      }
    }
    &__col-2 {
      width: 100%;
    }
  }

  .connect {
    flex-direction: column;
    width: 90%;
    margin-top: 1.6rem;
  }
}
